.Upload-button {
  background-color: #242e40 !important;
  color: white !important;
  padding: 0.4rem 1rem;
  border-radius: 4px;
  border: none !important;
  margin: 0.3rem;
  width: 100%;
}
.file-uploader-label-button {
  background-color: #242e40 !important;
  color: white !important;
  padding: 0.3rem 1rem;
  border-radius: 4px;
  border: none !important;
  margin: 0.3rem;
  width: 100% !important;
}
.fileUploader-inputs-buttons {
  display: flex;
  justify-content: flex-end !important;
  align-items: flex-end;
  flex-direction: column;
}
input[type="file"] {
  display: none;
}
.menuDrop-main-div,
.fileUploader-main-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-top: 1rem;
  border-bottom: 0.01px solid rgba(0, 0, 0, 0.452);
  padding: 0.5rem;
  width: 100%;
  background-color: #ebebeb;
}
.fileUploader-main-div > h1 {
  margin: 0.3rem !important;
  color: rgba(0, 0, 0, 0.4) !important;
  font-size: 1.2rem !important;
}
.selected-jop-title {
  margin: 0.3rem !important;
  color: rgba(0, 0, 0, 0.6) !important;
}
.menuDrop-main-div > h1 {
  margin: 0.3rem !important;
  color: rgb(0, 0, 0, 0.6) !important;
}
