@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
.popup-para-div {
  color: black;
  padding: 2rem 1.5rem;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Comfortaa1" !important;
}

.popup-h1 {
  text-align: center;
  color: #070707;
  margin-bottom: 1rem;
  font-weight: 600;
  font-family: "Aristotelica3" !important;
}

.popup-h4 {
  text-align: center;
  color: rgb(156, 33, 100);
  margin-bottom: 3rem;
}

.learn-more-button {
  font-weight: 500;
  font-size: 0.8rem;
  font-family: "Montserrat", sans-serif !important;
  margin: 0.5rem 0;
  color: rgb(0, 0, 0) !important;
  border-radius: 999999px !important;
  background-color: #f8b54a !important;
  padding: 0.8rem 1.2rem !important;
  text-decoration: none !important;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border: none !important;
  z-index: 111 !important;
}

.learn-more-button:hover {
  opacity: 0.8;
}

.popup-close-button,
.join-us-form-button-apply {
  color: rgb(0, 0, 0, 0.7) !important;
  background-color: #f8b54a !important;
  border-radius: 999999px !important;
  text-decoration: none !important;
  text-align: center !important;
  padding: 0.5rem 2rem !important;
  text-transform: capitalize !important;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px !important;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 111 !important;
}
.menu-drop-list-button {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.menu-drop-div {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-top: 1rem;
  border-bottom: 0.01px solid rgba(0, 0, 0, 0.452);
  padding: 0.5rem;
  width: 100%;
  background-color: #ebebeb;
}
.menu-drop-h1-title {
  padding: 1rem 0;
  margin: 0.3rem !important;
  color: rgba(0, 0, 0, 0.4) !important;
  font-size: 1.2rem !important;
  width: 100% !important;
}
.menu-drop-list-button > button {
  background-color: #242e40 !important;
  border: none !important;
  padding: 0.3rem 1.5rem !important;
  border-radius: 3% !important;
  text-align: center !important;
}
.popup-close-button:hover {
  opacity: 0.8;
}
.close-button-div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  padding: 1rem 0;
  margin-top: 1.5rem;
}
.h1-submit {
  color: #070707 !important;
}
.submitted-screen-row {
  background-color: #242e40;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.submitted-screen-h1 {
  color: white !important;
  font-size: 3rem;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.1);
}
.submitted-screen-icon {
  font-size: 3rem !important;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.1) !important;
  color: rgb(41, 151, 41) !important;
}
.fullName-textfield {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  flex-direction: row;
}
.join-us-text-field-firstName {
  width: 90% !important;
  margin-right: 0.5rem !important;
}
.join-us-text-field-lastName {
  width: 90% !important;
  margin-left: 0.5rem !important;
}
@media screen and (max-width: 1200px) {
}
