body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: "Aristotelica1";
  src: local("Aristotelica1"),
    url(../src/fonts/Aristotelica-Family-CC-BY-NCLicensepdf.pdf)
      format("truetype");
}
@font-face {
  font-family: "Aristotelica2";
  src: local("Aristotelica2"),
    url(../src/fonts/Aristotelica\ Display\ DemiBold\ Trial.ttf)
      format("truetype");
}
@font-face {
  font-family: "Aristotelica3";
  src: local("Aristotelica3"),
    url(../src/fonts/Aristotelica\ Display\ ExtraLight\ Trial.ttf)
      format("truetype");
}
@font-face {
  font-family: "Aristotelica4";
  src: local("Aristotelica4"),
    url(../src/fonts/AristotelicaSmallCaps-Regular.ttf) format("truetype");
}
@font-face {
  font-family: "Comfortaa1";
  src: local("Comfortaa1"),
    url(../src/fonts/Comfortaa-Bold.ttf) format("truetype");
}
@font-face {
  font-family: "Comfortaa3";
  src: local("Comfortaa3"),
    url(../src/fonts/Comfortaa-Light.ttf) format("truetype");
}
@font-face {
  font-family: "Comfortaa4";
  src: local("Comfortaa4"),
    url(../src/fonts/Comfortaa-Regular.ttf) format("truetype");
}
