@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");

.blog {
  font-family: "Comfortaa1";
}

.blog-allTheNew-row {
  background: rgb(255, 255, 255);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 33%,
    #f8b54a 33%,
    #f8b54a 50%,
    rgba(255, 255, 255, 1) 50%
  );
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.blog-allTheNew-4-left {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}
.blog-allTheNew-4-middle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.blog-allTheNew-4-right {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.blog-allTheNew-title-col {
  margin-bottom: 1rem;
}

.blog-allTheNew-h1-title {
  font-size: 60px;
  font-weight: 600;
  font-family: "Aristotelica3";
}

.blog-allTheNew-h1-title-span {
  color: #f8b54a;
}

.blog-allTheNew-itemCard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.blog-allTheNew-image {
  border-radius: 1%;
  position: relative;
  margin-top: 1rem;
  border-radius: 4% !important;
}

.blog-overPhohto-content-div {
  color: white;
  background: linear-gradient(to top, #363e50da 0%, #ffffff00 100%);
  padding: 0rem 0;
  padding: 2rem 0;
  position: absolute;
  bottom: 0rem;
  width: 250px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  height: 100%;
  border-radius: 4% !important;
}
.blog-overPhohto-content-div-middle {
  color: white;
  background: linear-gradient(to top, #363e50da 0%, #ffffff00 100%);
  padding: 0rem 0;
  padding: 2rem 0;
  position: absolute;
  bottom: 0rem;
  width: 300px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  height: 100%;
  border-radius: 4% !important;
}

.blog-overPhohto-content-para {
  max-width: 20rem;
  text-align: center;
  padding: 0 1rem;
  transition: ease-in-out;
  font-size: 15px;
  font-family: "Comfortaa1" !important;
}

.blog-overPhohto-content-buttonAndIcon-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.blog-overPhohto-content-button {
  background-color: #363e50 !important;
  color: white !important;
  border-radius: 0px !important;
  padding: 0.2rem 1.2rem !important;
  border: none !important;
}

.blog-recentPosts-main-row {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(243, 243, 243);
  padding-bottom: 2rem;
}

.blog-recentPosts-h1-title {
  font-size: 60px;
  font-weight: 600;
  font-family: "Aristotelica3";
  padding: 3rem 2rem;
  border-bottom: 3px solid #f8b54a;
}

.blog-recentPosts-col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 2rem;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.blog-recentPosts-upperimage {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.blog-recenetPosts-text-content-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 370px;
  padding: 1rem 0;
}

.blog-recentPosts-subtitle-div {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}

.blog-recentPosts-profilePic-image {
  margin: 0.5rem 0;
  margin-right: 1rem;
}
.blog-recenetPosts-text-content-div > h3 {
  font-size: 20px !important;
}
.blog-recenetPosts-text-content-div > p {
  font-size: 12px !important;
}
@media screen and (max-width: 1100px) {
  .blog-recenetPosts-text-content-div {
    padding: 1.5rem;
  }
}
