@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");

.about {
  font-family: "Aristotelica3" !important;
  background-color: transparent;
}
.navBar-row-space {
  padding: 2.5rem 0;
  background-color: rgb(40, 38, 96, 0.95) !important;
  width: 100%;
  margin: 0 !important;
  z-index: 20 !important;
}
.about-us-fp-mainRow {
  background: url(https://i.ibb.co/n31Knh3/about-us-mp.jpg) no-repeat center;
  background-size: contain;
  background-attachment: fixed;
  background-position: left;
  width: 100%;
  min-height: 41rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.about-us-fp-firstPart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  background-color: rgb(121, 143, 156, 0.8);
  border-radius: 100%;
  padding: 2.5rem 0rem;
}

.about-us-fp-firstPart-content-div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
  flex-direction: column;
  padding-left: 2rem;
}

.about-us-fp-h1-title {
  font-family: "Aristotelica3" !important;
  font-size: 50px;
  padding: 1rem 0rem;
  text-align: left;
  font-weight: 600;
}

.about-us-fp-para-text {
  padding: 1rem 0rem;
  font-family: "Comfortaa1" !important;
  max-width: 25rem;
}

.about-us-fp-button {
  margin: 2rem 0 !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: #282660 !important;
  padding: 0.4rem 2.3rem !important;
}
.about-us-fp-button2 {
  margin: 2rem 0 !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: rgb(156, 33, 100) !important;
  padding: 0.4rem 2.3rem !important;
}

.about-us-fp-button-editedCss {
  margin: 2rem 0 !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: rgb(121, 143, 156) !important;
  padding: 0.4rem 2.3rem !important;
}

.howWeWork-row-container {
  margin-top: 2rem;
  margin-bottom: 4rem;
}

.howWeWork-leftSide-backgroundImage-overlay {
  background: url(https://i.ibb.co/4m9FSnB/about-us-fp-how-We-Workleft-Side.jpg)
    no-repeat center;
  background-size: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
}

.howWeWork-leftSide-backgroundColor-overlay {
  background-color: rgb(38, 38, 96, 0.8);
  z-index: 2;
  width: 100%;
  height: 100%;
  padding: 0;
}

.howWeWork-leftSide-content {
  color: white;
  padding: 3rem;
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.about-us-fp-h5-our-capabilities {
  font-size: 50px !important;
  font-weight: 600;
}
.about-us-fp-h4-howWeWork {
  font-size: 20px !important;
  font-family: "Comfortaa1";
}
.howWeWork-rightSide-backgroundImage-overlay {
  background: url(https://i.ibb.co/9v4sy46/how-We-Work-right-Side.jpg) no-repeat
    center;
  background-size: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
}

.howWeWork-rightSide-backgroundColor-overlay {
  background-color: rgb(156, 33, 100, 0.8);
  z-index: 2;
  width: 100%;
  height: 100%;
  padding: 0;
}

.partner-span {
  color: #f8b54a;
}

.howWeWork-rightSide-content {
  color: white;
  padding: 3rem;
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.howWeWork-leftSide-col {
  padding: 0 !important;
}

.howWeWork-rightSide-col {
  padding: 0 !important;
}

.about-us-fp-h4-howWeWork {
  max-width: 25rem;
  font-family: "Aristotelica3" !important;
}

.about-us-fp-our-capabilities-upper-row {
  margin-top: 6rem;
}
.about-us-fp-our-capabilities-header-col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.about-us-fp-our-capabilities-header-h1 {
  font-size: 50px;
  font-weight: 600;
}
.about-us-fp-our-capabilities-header-p {
  max-width: 55rem;
  text-align: center;
  font-family: "Comfortaa1";
  font-size: 15px;
}
.about-us-fp-our-capabilities-lower-row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
}
.about-us-fp-our-capabilities-body-card-col {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 2rem;
}
.about-us-fp-our-capabilities-body-card-num-col {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0 !important;
  width: 100px;
  height: 100px;
  content: "";
}
.about-us-fp-our-capabilities-body-card-contnet-col {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
.about-us-fp-our-capabilities-body-card-h1-title {
  font-size: 25px;
  font-weight: 600;
}
.capbabilities-icons {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border-radius: 20%;
  margin-bottom: 0.5rem;
  margin-left: 0.3rem;
}
.a-icon-copyright {
  color: rgba(0, 0, 0, 0.815) !important;
  text-decoration: none !important;
}
.about-us-fp-our-capabilities-body-card-p-text {
  font-size: 15px;
  font-family: "Comfortaa1";
}
.about-us-fb-team-main-row {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 32%,
    #f8b54a 32%,
    #f8b54a 59%,
    rgba(255, 255, 255, 1) 59%
  );
  padding: 2rem 0;
  margin-bottom: 4rem;
}
.about-us-fb-team-title-col {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.about-us-fb-team-h1-title {
  font-size: 50px;
  font-weight: 600;
}
.about-us-fb-team-card-col {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: white;
  padding: 3rem 0;
  margin: -1rem 0;
  width: 100%;
  height: 100%;
}
.about-us-fb-team-card-image-col {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.about-us-fb-team-card-text-content-col {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
  flex-direction: column;
}
.about-us-fb-team-card-text-content-h4 {
  font-size: 25px;
  font-weight: 600;
}
.about-us-fb-team-card-text-content-h6 {
  color: #9c2164;
  font-size: 18px;
  font-weight: 600;
}
.about-us-fb-team-card-text-content-p {
  max-width: 35rem;
  font-size: 12px;
  font-family: "Comfortaa1";
}
.about-us-fb-icon-inner-div {
  margin-right: 0.4rem;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  text-align: center !important;
  width: 100%;
}
.about-us-fb-team-button {
  background-color: #f8b54a !important;
  margin: 1rem 0 !important;
  border-radius: 99999px !important;
}
.tele-link-nav2 {
  color: black !important;
  text-decoration: none !important;
}

@media screen and (max-width: 1200px) {
  .about-us-fp-h1-title {
    letter-spacing: 0.2rem;
    font-size: 2.1rem;
    padding: 1rem;
  }

  .about-us-fp-para-text {
    padding: 0rem;
  }

  .about-us-fp-mainRow {
    background: url(https://i.ibb.co/0c3yHNK/About-Us-FPBackground.jpg)
      no-repeat center;
    background-size: cover;
    background-position: center;
    margin: 0 !important;
    background-attachment: fixed;
    min-height: 100%;
  }

  .about-us-fp-firstPart {
    flex-direction: column;
    background-color: rgba(210, 171, 44, 0.75);
    border-radius: 0%;
    width: 100%;
    padding: 2rem 0rem;
  }

  .about-us-fp-h5-our-capabilities {
    padding-bottom: 0rem;
    letter-spacing: 0rem;
    font-family: "Aristotelica3" !important;
    font-size: 2rem;
  }

  .about-us-fp-our-capabilities-leftSide {
    width: 100%;
    height: 100%;
    padding: 2.5rem;
    box-shadow: none;
  }

  .about-us-fp-our-capabilities {
    padding: 0;
  }

  .about-us-fp-h2-text {
    max-width: 20rem;
    font-size: 0.9rem !important;
    padding-left: 0rem;
    padding-top: 2.5rem;
    line-height: 1.5rem;
  }

  .about-us-fp-our-capabilities-item {
    padding: 0rem 1.2rem;
    margin: 0;
    color: rgba(210, 171, 44, 1);
    font-size: 0.8rem;
    box-shadow: none;
    border: 2px dashed white;
    margin: 0.5rem 0.5rem;
    border-radius: 99999px;
  }

  .about-us-fp-h4-howWeWork {
    max-width: 20rem;
    text-align: center;
    padding-top: 1rem;
    font-size: 1rem;
  }
}
