.it-team-main-row {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem;
}
.it-team-left-col {
  background-color: rgb(121, 143, 156, 0.05);
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.it-team-left-col-span-meet {
  font-family: "Comfortaa1";
}
.it-team-left-col-h1-title {
  font-family: "Aristotelica3";
  font-weight: 600;
  font-size: 60px;
  letter-spacing: 0.3rem;
  text-align: center;
}
.it-team-left-col-p-text {
  font-family: "Comfortaa1";
  font-size: 15px;
}
.it-team-images-cols-row-1 {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.it-team-images-cols-row-2 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.it-team-images-cols-row-3 {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.it-team-images-col {
  display: flex;
  margin: 0 !important;
  justify-content: center;
  align-items: center;
}

.it-team-image-div-color-1::before {
  width: 120px;
  height: 120px;
  content: "";
  position: absolute;
  z-index: 10;
  background: rgb(156, 33, 100, 0.5);
}
.it-team-image-div-color-2::before {
  width: 120px;
  height: 120px;
  content: "";
  position: absolute;
  z-index: 10;
  background: rgb(248, 181, 74, 0.5);
}
.it-team-image-div-color-3::before {
  width: 120px;
  height: 120px;
  content: "";
  position: absolute;
  z-index: 10;
  background: rgb(94, 43, 132, 0.5);
}
.it-team-lower-row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 8rem;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 17%,
    #f8b54a 17%,
    #f8b54a 60%,
    rgba(255, 255, 255, 1) 60%
  );
}
.it-team-lower-row-title-col {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.it-team-lower-row-title-col-h1 {
  font-family: "Aristotelica3";
  font-weight: 600;
  font-size: 50px;
  padding: 0 3rem;
}
.it-team-lower-row-card-col {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  border: 2px solid #e9e9e9;
  padding: 1rem;
  margin: 1rem;
  background-color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.it-team-lower-row-card-leftSide {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
.it-team-lower-row-card-h2-title {
  font-family: "Aristotelica3";
  font-weight: 600;
  font-size: 30px;
}
.it-team-lower-row-card-position-span {
  font-size: 15px;
  font-family: "Comfortaa1";
}
.it-team-lower-row-card-experience-span {
  font-size: 12px;
  font-family: "Comfortaa1";
}
.it-team-lower-row-card-rightSide {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
.card-rightSide-image {
  margin-bottom: 1rem;
}
