.join-us {
  color: rgb(0, 0, 0);
  margin-bottom: 5rem;
  font-family: "Comfortaa1" !important;
}

.join-our-team-h1-title {
  font-size: 3rem;
  padding-bottom: 3rem;
  padding-top: 1rem;
  font-family: "Aristotelica3" !important;
}

.job-on-board-card-para-content {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Comfortaa1";
}

.job-vacancies-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  color: white;
}

.job-vacancies-rightSide {
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.apply-button {
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: rgb(248, 181, 74) !important;
  padding: 0.1rem 1rem !important;
  text-decoration: none !important;
}

.job-vacancies-leftSide-col {
  display: flex;
  justify-content: center;
  align-items: center;
}

.join-us-main-secondRow2 {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 73%,
    rgb(248, 181, 74) 73%
  );
}

.secondRow2-1-cols-button,
.secondRow2-1-cols-textarea {
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.join-us-phone-number-input {
  color: white !important;
}

.inputfile {
  cursor: pointer;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputfile + label {
  padding: 0rem;
  color: rgb(0, 0, 0);
  background-color: transparent;
}

.inputfile + label {
  cursor: pointer;
}

.file-input-div {
  padding: 2rem 0rem;
  margin-top: 2rem;
  border-radius: 999999px;
  background-color: #ffffff;
  text-align: center;
}

.join-us-leftSide-inner-col2 {
  color: rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  margin-left: 1rem;
}

.join-us-secondRow2-col {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}

.join-us-leftSide-inner-col2-h1 {
  margin: 0 !important;
  padding-top: 1.2rem;
  font-size: 3.5rem;
  font-weight: 600;
  letter-spacing: 0.2rem;
  font-family: "Aristotelica3" !important;
  text-align: center !important;
}
.join-us-rightSide-main-col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.job-vacancies-rightSide-col {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  padding: 3rem;
  background-color: #242e40;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.job-vacancies-buttons-div {
  width: 100%;
  padding: 0.5rem 0 !important;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
}
.join-us-job-vacancies-cards-row {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 0;
  width: 100%;
}
.join-us-job-vacancies-card-title-div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
  width: 100%;
}

.secondRow2-cols-1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.secondRow2-1-cols {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.join-us-main-secondRow2-col {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.join-us-text-field {
  margin: 1rem 0 !important;
}
.join-us-form-button {
  font-size: 0.8rem !important;
  font-family: "Montserrat", sans-serif !important;
  margin: 0.5rem 0;
  color: rgb(0, 0, 0) !important;
  border-radius: 999999px !important;
  background-color: #f8b54a !important;
  padding: 0.2rem 1.6rem !important;
  text-decoration: none !important;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border: none !important;
}
/*-----------Menu Drop-------------*/
.choose-from-listed-jobs {
  background-color: #f8b54a !important;
  color: white !important;
}
.choose-jop-position-button {
  background-color: #242e40 !important;
  color: white !important;
  border: none !important;
}
.selected-jop-title {
  font-size: 30px !important;
  font-family: "Aristotelica3" !important;
  font-weight: 600 !important;
  text-align: left !important;
  line-height: 0rem !important;
  padding: 1.1rem 0;
}
@media screen and (max-width: 1200px) {
  .join-us-mainRow {
    background: linear-gradient(to top, #bb9723 50%, #ffffff 50%);
    padding: 0rem;
  }

  .join-our-team-h1-title {
    font-size: 2.1rem;
    padding-bottom: 1rem;
    padding-top: 1rem;
    font-family: "Montserrat", sans-serif;
  }

  .job-on-board-card {
    color: white;
    margin: 0rem;
    box-shadow: none;
    padding: 2rem;
  }

  .job-on-board-card-h1-title {
    font-size: 1.5rem;
    text-align: center;
  }

  .jobOnBoard-subtitle-text {
    font-size: 0.8rem;
    max-width: 20rem;
  }

  .job-vacancies-rightSide {
    padding: 0rem;
  }

  .thirdRow4-cols2-h4 {
    font-size: 1.1rem;
    padding: 1rem 0;
  }

  .thirdRow4-cols2-h6 {
    font-size: 0.8rem;
  }

  .job-vacacies-card-para-text {
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 14rem;
  }

  .job-vacancies-buttons-div {
    transform: scale(0.75);
    padding-bottom: 1rem;
  }

  .job-vacancies-leftSide-images {
    width: 200%;
    height: 100%;
    padding: 1rem 0;
  }

  .join-us-main-secondRow2 {
    margin: 3rem 0 !important;
  }

  .join-us-secondRow2-title {
    font-size: 1.5rem;
    color: rgb(255, 255, 255);
    font-family: "Montserrat", sans-serif;
    padding-bottom: 1rem;
    letter-spacing: 0.1rem;
  }
}
