@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");

.employment {
  font-family: "Aristotelica3";
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: rgba(255, 255, 255, 1);
  padding: 5rem 2rem !important;
}

.employment-firstRow {
  background-color: #242e40;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 5rem 0rem;
  border-radius: 999999px;
}

.firstRow-cols-employment {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.cols-title-employment {
  font-size: 60px !important;
  font-family: "Aristotelica3" !important;
}

.cols-title-span-employment {
  color: rgb(248, 181, 74);
}

.joinOurTeamLink {
  text-decoration: none !important;
  color: rgb(255, 255, 255) !important;
}
.firstRow-cols-employment {
  font-size: 30px !important;
  padding-bottom: 2rem;
  font-family: "Comfortaa1";
}
.employment-button {
  background-color: #2b3546 !important;
}
@media screen and (max-width: 1200px) {
  .employment {
    padding: 1rem 0rem !important;
  }

  .employment-firstRow {
    padding: 3rem 0rem;
    border-radius: 0px;
  }

  .cols-title-employment {
    font-size: 2.1rem !important;
    padding-bottom: 1rem;
  }

  .firstRow-cols-employment {
    font-size: 1.1rem !important;
    padding-bottom: 2rem;
  }
}
